  import goodsSwiper from '../../components/swiper.vue'
  import goodsFooter from './components/footer.vue'
  import goodsSelected from './components/selected.vue'
  import CubePage from '../../components/cube-page.vue'
  export default {
    name: 'GoodsIndex',
    data (){
      return {
        cartNum: 0,
        maskShow: false,
        selected: false,
        goods: {},//
        lists: [],// //
        banner: [],//
        isBall: false,//
        collect: 0, //
        isShare: false,
        number: 1,
      }
    },
    components: {
      goodsSwiper,
      goodsFooter,
      goodsSelected,
      CubePage
    },
    methods: {
      reduce(){
        if(this.number<=1){
          this.showToastTxtOnly('')
          return false
        }else{
          this.number -=1;
        }
      },
      //
      plus(stock){
        if(this.number>=stock) {
          this.showToastTxtOnly('')
          return false
        }else{
          this.number +=1
        }
      },
      //
      handleNumChange(stock){
        if(this.number>=stock){
          this.showToastTxtOnly('')
          this.number = stock
        }
      },
      //
      shareClick(){
        this.isShare = true
      },
      //
      changeCloseShare(){
        this.isShare = false
      },
      showToastTxtOnly(text) {
        this.toast = this.$createToast({
          txt: text,
          type: 'txt'
        })
        this.toast.show()
      },
      //
      collectionClick(id){
        this.http.post(this.ports.goods.collection,{goods_id: id}, res =>{
          if(res.success){
            let data = res.data
            this.collect = true
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //
      cancelClick(id){
        this.http.delete(this.ports.goods.collection+'/'+id, res =>{
          if(res.success){
            this.collect = false
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //
      beforeEnter(el){
        el.style.transform = "translate(0, 0)"
      },
      enter(el, done){
        // el.offsetWidth html
        // el.offsetWidth 
        el.offsetWidth;
        el.style.transform = "translate(-70px, 68vh) scale(0.01)";
        el.style.transition = "all 1s ease";
//        console.log(done);
        done()
      },
      afterEnter(el){
//        this.flag = !this.flag
      },
      //
      getIndexData(){
        let id = this.$route.params.id
        this.http.get(this.ports.integral.points+'/'+id, res =>{
          this.$store.commit('changeLoading',false)
          if(res.success){
            let data = res.data
            this.goods = data
            this.lists = data.lists
            this.banner = data.image
            this.collect = data.collect
            console.log(this.goods)
          }else{
            this.showToastTxtOnly(res.msg)
          }
        })
      },
      //
      confirmClick(){
        this.isBall = false
        this.maskShow = true
        const that = this
        setTimeout( ()=>{
          that.selected = true
        },200)
      },
      //
      changeAddCart(val,type){
        let that = this
        this.cartNum = val
        this.selected = false
        if(!this.selected){
          setTimeout(()=>{
            this.maskShow = false
            this.isBall = true
            if(type){
              that.$store.commit('changeCartNum',parseInt(this.$store.state.cartNum)+0)
            }else{
              that.$store.commit('changeCartNum',parseInt(this.$store.state.cartNum)+val)

            }
          },500)
        }
      },
      //
      changeSelected(val) {
        this.selected = false
        if(!this.selected){
          setTimeout(()=>{
            this.maskShow = false
          },500)
        }
      },
      exchangeClick(id){
        this.$router.push({path: '/integralOrder/orders',query:{
          goods_id: id,
          count: this.number
        }})
      }
    },
    mounted (){
      this.getIndexData()
    }
  }



